import React, { Component } from 'react';
import Gallery from "react-photo-gallery";
import Lightbox from 'react-images';

class Portfolio extends Component {

  constructor() {
      super();
      this.state = { currentImage: 0 };
      this.closeLightbox = this.closeLightbox.bind(this);
      this.openLightbox = this.openLightbox.bind(this);
      this.gotoNext = this.gotoNext.bind(this);
      this.gotoPrevious = this.gotoPrevious.bind(this);
    }
    openLightbox(event, obj) {
      this.setState({
        currentImage: obj.index,
        lightboxIsOpen: true,
      });
    }
    closeLightbox() {
      this.setState({
        currentImage: 0,
        lightboxIsOpen: false,
      });
    }
    gotoPrevious() {
      this.setState({
        currentImage: this.state.currentImage - 1,
      });
    }
    gotoNext() {
      this.setState({
        currentImage: this.state.currentImage + 1,
      });
    }

  render() {
    var photos = [];
    if(this.props.data){
      var projects = this.props.data.projects.map(function(projects){
        var projectImage = 'images/portfolio/'+projects.image;
        return <div key={projects.title} className="columns portfolio-item">
           <div className="item-wrap">
            <a href={projects.url} target="_blank" rel="noopener noreferrer" title={projects.title}>
               <img alt={projects.title} src={projectImage} />
               <div className="overlay">
                  <div className="portfolio-item-meta">
                 <h5>{projects.title}</h5>
                     <p>{projects.category}</p>
                  </div>
                </div>
              <div className="link-icon"><i className="fa fa-link"></i></div>
            </a>
          </div>
        </div>
      })
    }
    if (this.props.data){
      photos = [
      { src: this.props.data.personals1.src, width: this.props.data.personals1.thumbW, height: this.props.data.personals1.thumbH, caption: this.props.data.personals1.caption },
      { src: this.props.data.personals2.src, width: this.props.data.personals2.thumbW, height: this.props.data.personals2.thumbH, caption: this.props.data.personals2.caption },
      { src: this.props.data.personals3.src, width: this.props.data.personals3.thumbW, height: this.props.data.personals3.thumbH, caption: this.props.data.personals3.caption },
      { src: this.props.data.personals4.src, width: this.props.data.personals4.thumbW, height: this.props.data.personals4.thumbH, caption: this.props.data.personals4.caption },
      { src: this.props.data.personals5.src, width: this.props.data.personals5.thumbW, height: this.props.data.personals5.thumbH, caption: this.props.data.personals5.caption },
      { src: this.props.data.personals6.src, width: this.props.data.personals6.thumbW, height: this.props.data.personals6.thumbH, caption: this.props.data.personals6.caption },
      { src: this.props.data.personals7.src, width: this.props.data.personals7.thumbW, height: this.props.data.personals7.thumbH, caption: this.props.data.personals7.caption },
      { src: this.props.data.personals8.src, width: this.props.data.personals8.thumbW, height: this.props.data.personals8.thumbH, caption: this.props.data.personals8.caption },
      { src: this.props.data.personals9.src, width: this.props.data.personals9.thumbW, height: this.props.data.personals9.thumbH, caption: this.props.data.personals9.caption }
      ];
  }


    return (
      <section id="portfolio">

      <div className="row">

         <div className="twelve columns collapsed">

            <h1>Check Out Some of My Work at Sizmek</h1>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                {projects}
            </div>

            <h1>Check Out Some of my Personal Work</h1>
            <div>
            <Gallery photos={photos} onClick={this.openLightbox} />
            <Lightbox images={photos}
              onClose={this.closeLightbox}
              onClickPrev={this.gotoPrevious}
              onClickNext={this.gotoNext}
              currentImage={this.state.currentImage}
              isOpen={this.state.lightboxIsOpen}
            />
          </div>
          </div>

      </div>
   </section>
    );
  }
}

export default Portfolio;
